.table_container {
  display: flex;
  flex-direction: column;
  width: 97%;
  margin: 0;
  height: 100%;
  min-height: 400px;
  overflow-x: scroll;
  overflow-y: hidden;
}

.table {
  margin: 10px 30px;
  border-spacing: 0px;
  width: 100%;
  height: 100%;
  overflow-x: scroll;
  font-family: "Inter", sans-serif;
}

.table_head_row {
  height: 45px;
}

.table_head_title {
  font-size: 0.75rem;
  line-height: 16px;
  font-weight: 800;
  color: #1e1e1e;
  font-family: "Inter", sans-serif;
  text-align: right;
  border-top: 1px solid #eff2f5;
  border-bottom: 1px solid #eff2f5;
  padding: 10px;
}

.first-column {
  text-align: center;
  width: 25px;
  min-width: 25px;
  max-width: 25px;
}

.second-column {
  text-align: left;
  width: 160px;
  min-width: 160px;
  max-width: 160px;
}

.third-column {
  width: 90px;
  min-width: 90px;
  max-width: 90px;
}

.four-column {
  width: 55px;
  min-width: 55px;
  max-width: 55px;
}

.six-column {
  width: 130px;
  min-width: 130px;
  max-width: 130px;
}

.seven-column {
  width: 140px;
  min-width: 140px;
  max-width: 140px;
}

.eigth-column {
  width: 170px;
  min-width: 170px;
  max-width: 170px;
}

.nine-column {
  width: 160px;
  min-width: 160px;
  max-width: 160px;
}

.table_tbody_row {
  height: 70px;
}

.table_tbody_row:hover {
  background-color: rgb(239, 242, 245);
}

.table_tbody_coin {
  font-family: "Inter", sans-serif;
  font-size: 0.875rem;
  font-weight: 600;
  color: #1e1e1e;
  border-bottom: 1px solid #eff2f5;
  text-align: right;
  padding: 10px;
}

.table_tbody_coin.first-column {
  text-align: center;
}

.table_body_coin_volume {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.coin_volume_ticker {
  font-size: 0.75rem;
  color: #9e9e9e;
}

.table_body_coin_name {
  display: flex;
  gap: 5px;
}

.green {
  font-weight: 600;
  color: #209269;
}

.red {
  font-weight: 600;
  color: #B74927;
}

.ratio_block {
  display: flex;
  align-items: center;
  justify-content: right;
}

.ratio_logo {
  width: 10px;
  margin-right: 2px;
}

.text_coin {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.text_coin_logo {
  width: 24px;
}

.text_coin_name {
  font-family: "Inter", sans-serif;
  font-size: 0.875rem;
  line-height: 1rem;
  font-weight: 600;
}

.table_body_coin_name .text_coin_symbol {
  font-size: 0.875rem;
}

.text_coin_rank {
  display: none;
}

.text_coin_symbol {
  color: #9e9e9e;
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 1rem;
}

.text_coin_symbol_supply {
  font-size: 0.85rem;
  font-weight: 600;
  line-height: 1rem;
  margin-left: 3px;
}

.sparkline {
  width: 160px;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5.5rem;
}

.loader {
  position: sticky;
  z-index: 200;
  width: 60px;
  height: 60px;
  border: 5px solid #406485;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media (max-width: 600px) {
  table {
    display: table;
    overflow-x: scroll;
    border-spacing: 0px;
    font-family: "Inter", sans-serif;
  }
  thead {
    position: relative;
    z-index: 120;
    border-spacing: 0px;
  }
  /* .table{
    margin: 0 10px 0 0;
  } */
  .table_container {
    width: 94%;
    margin: 0 auto;
  }
  .table {
    margin: 0;
  }
  .table_head_title.sticky {
    background-color: #FFFFFF;
  }
  .table_head_title {
    font-size: 0.75rem;
    padding: 10px 4px;
    overflow: hidden;
    white-space: nowrap;
    line-height: 0;
    vertical-align: middle;
    font-weight: 700;
  }
  .sticky {
    left: 0;
    position: sticky;
    z-index: 100;
  }
  .first-column {
    display: none;
  }
  .second-column {
    text-align: left;
    width: 135px;
    min-width: 135px;
    max-width: 135px;
    left: 0;
    padding-left: 15px;
  }
  .third-column {
    width: 100px;
    min-width: 100px;
    max-width: 100px;
  }
  .four-column {
    width: 56px;
    min-width: 56px;
    max-width: 56px;
  }
  .six-column {
    width: 110px;
    min-width: 110px;
    max-width: 110px;
  }
  .seven-column {
    width: 140px;
    min-width: 140px;
    max-width: 140px;
  }
  .eigth-column {
    width: 170px;
    min-width: 170px;
    max-width: 170px;
  }
  .nine-column {
    width: 100px;
    min-width: 100px;
    max-width: 100px;
  }
  .table_tbody_row {
    height: 66px;
    overflow: hidden;
  }
  .table_tbody_coin {
    padding: 5px;
    font-size: 0.875rem;
    font-weight: 500;
  }
  .table_tbody_coin.sticky {
    background-color: #FFFFFF;
    text-align: center;
    position: sticky;
    z-index: 100;
  }
  .table_body_coin_name {
    display: flex;
    flex-direction: column;
    gap: 1px;
    text-align: left;
  }
  .text_coin {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    margin-left: 10px;
  }
  .text_coin_logo {
    width: 24px;
  }
  .text_coin_name_block {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .text_coin_name {
    font-size: 0.875rem;
    line-height: 1rem;
    font-weight: 700;
  }
  .text_coin_rank {
    display: flex;
    background-color: rgb(239, 242, 245);
    color: rgb(88, 102, 126);
    font-size: 12px;
    padding: 2px 6px;
    border-radius: 4px;
  }
  .text_coin_symbol {
    color: #9e9e9e;
    font-size: 12px;
    font-weight: 600;
    line-height: 1rem;
    margin-left: 6px;
  }
  .green {
    font-weight: 600;
    color: #209269;
  }
  .red {
    font-weight: 600;
    color: #B74927;
  }
  .ratio_logo {
    width: 8.5px;
    margin-right: 2px;
  }
  .text_coin_symbol_supply {
    font-size: 0.85rem;
    font-weight: 600;
    line-height: 1rem;
    margin-left: 3px;
  }
  .sparkline {
    width: 160px;
  }
}

